import { useQuery } from "react-query";
import { fetchFaqsFunction } from "../../api/api";
import { LoadingComponent } from "../reusable_components/loading-content";
import { ErrorAlert } from "../reusable_components/alerts/alerts";
import { LoadPageAnimation } from "../reusable_components/load_page_animation";
import { useEffect } from "react";

export default function FrequentlyAskedQuestions() {
  const {
    data: faqs,
    error: faqsErrors,
    isError: isFaqsError,
    isLoading: isFaqsLoading,
  } = useQuery({
    queryKey: ["faqs"],
    queryFn: fetchFaqsFunction,
  });

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  if (isFaqsLoading) {
    return (
      <LoadingComponent secondary_text={"Loading Frequently asked questions"} />
    );
  }

  if (isFaqsError) {
    return <ErrorAlert secondaryText={`${faqsErrors.response.data}`} />;
  }

  return (
    <LoadPageAnimation>
      <main className="max-w-xl mx-auto p-6 my-6 text-black bg-white">
        <div className="rounded-md p-6 shadow-sm border border-gray-200">
          <h2 className="text-start text-black px-2 font-extrabold text-3xl">
            Frequently asked questions
            <p className="text-sm font-normal text-slate-800 pt-2">
              Thank you for trusting Reload Logistic Services with your personal
              information.
            </p>
          </h2>
          {/* <p className="text-center text-gray-400">
          No frequently asked questions at this point
        </p> */}
          <ul className="px-2 pt-4">
            {faqs?.results?.map((faq, index) => {
              return (
                <li key={index}>
                  <p className="text-sm font-bold">
                    {index + 1}. {faq.question}
                  </p>
                  <p className="text-xs text-gray-600 py-1 pl-3">
                    {faq.answer}
                  </p>
                </li>
              );
            })}
          </ul>
        </div>
        <div className="mb-20" />
      </main>
    </LoadPageAnimation>
  );
}
